import React, { Component } from 'react';
import { Consumer } from '../Context';
import {
  Container,
  Menu,
  Grid,
  Button,
  Sidebar
} from "semantic-ui-react";

export default class SideBar extends Component {
  render() {
    return (
      <Consumer>
        {state => (
          <Sidebar
            as={Menu}
            animation={'overlay'}
            direction={'left'}
            icon='labeled'
            inverted
            vertical
            visible={state.menuVisible}
            width='very wide'
          >
            <Container>
              <Grid stackable>
                <Grid.Column textAlign="right">
                  <div style={{ float: 'right' }} onClick={state.actions.toggleMenu} className="hamburger-nav open">
                    <span></span>
                    <span></span>
                    <span style={{ top: '7px' }}></span>
                    <span></span>
                  </div>
                </Grid.Column>
                <Grid.Column>
                  {
                    state.menus.map((menu, index) => (

                      <h3 key={index}>
                        {
                          menu.type === 'text' ? (
                            <a onClick={state.actions.toggleMenu} href={menu.link}>{menu.text}</a>
                          ) : (
                              <span className="button">{menu.text}</span>
                            )
                        }
                      </h3>
                    ))
                  }
                </Grid.Column>
              </Grid>
            </Container>
          </Sidebar>
        )}
      </Consumer>
    )
  }
}
