import React, { Component } from 'react'
import { Consumer } from '../Context';
import logo from '../images/logo.png';
import {
    Menu,
    Image,
    Grid
} from "semantic-ui-react";

export class NavMenu extends Component {
    render() {
        return (
            <Consumer>
                {state => (
                    <Menu vertical={false} secondary className="nav">
                        <Menu.Menu position="left">
                            <Menu.Item style={{ paddingRight: '0' }}>
                                <Image className="logo" src={logo} circular />
                            </Menu.Item>
                            <Menu.Item style={{ width: 'fit-content' }}>
                                <Grid>
                                    <Grid.Row style={{ padding: '0' }}>
                                        <Grid.Column className="logo-name">
                                            Siegfred Pagador
                                        </Grid.Column>
                                    </Grid.Row>
                                    <Grid.Row style={{ padding: '0' }}>
                                        <Grid.Column className="logo-title">
                                            Full-Stack Web Developer
                                        </Grid.Column>
                                    </Grid.Row>
                                </Grid>
                            </Menu.Item>
                        </Menu.Menu>
                        <Menu.Menu position="right">
                            {
                                state.menus.map((menu, index) => (
                                    (
                                        menu.type === 'text' ? (
                                            <Menu.Item key={index} className="link"><a href={menu.link}>{menu.text}</a></Menu.Item>
                                        ) : (
                                                <Menu.Item key={index}>
                                                    <span className="button">{menu.text}</span>
                                                </Menu.Item>
                                            )
                                    )

                                ))
                            }
                            <Menu.Item className="more">
                                <div onClick={state.actions.toggleMenu} className="hamburger-nav"><span></span> <span></span> <span></span></div>
                            </Menu.Item>
                        </Menu.Menu>
                    </Menu>
                )}
            </Consumer>
        )
    }
}

export default NavMenu
